.card {
    max-width: 100%;
    width: 92rem;
    height: 48rem;
    background-color: $white-main;
    margin: 0 auto;
    border-radius: 1rem;
    box-shadow: 0px 10px 10px 10px rgba(0,0,0, 0.04);
    display: flex;
    justify-content: space-between;
    padding: 3.2rem;

    @include breakpoint(tablet) {
        flex-direction: column;
        align-items: center;
        height: fit-content;
    }
}